import Help from "./help-model.js";
const help = new Help()
export default {
	data() {
		return {
			helpList: [],
			selId: null,
			imgUrl: ''
		};
	},
	onLoad() {
		const that = this
		that._onLoad()
	},
	methods:{
		_onLoad(callBack) {
			const that = this
			that.getHelpsList(() => {
				callBack && callBack();
			})
		},
		// 用户信息获取
		getHelpsList(callBack) {
			const that = this
			help.getHelpsList({}, (res) => {
				if (res.status_code == 'ok') {
					that.helpList = res.data
				}
				callBack && callBack();
			})
		},
		selShow(e) {
		    this.selId = help.get_data_set(e, "id");
		  }
	}
}
